var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{attrs:{"id":"hoTroKhGt"}},[_c('v-container',[_c('div',[_c('h2',{staticClass:"text-subtitle-2 text-center mt-5"},[_vm._v(" Hướng Dẫn Mua Đàn Guitar Cho Người Mới Tập Chơi ")]),_c('p',{staticClass:"text-sm-left mt-5"},[_vm._v(" Nếu bạn đang quan tâm tới bài viết này, thì chắc hẳn, bạn là người mới tập chơi đàn guitar, hoặc đang ở giai đoạn tìm hiểu để mua cho mình 1 cây đàn guitar tốt, đúng không ? ")]),_c('p',{staticClass:"text-md-left"},[_vm._v(" Với những người mới chơi đàn guitar như bạn, thì việc bấm phím đàn guitar, cảm thụ âm nhạc, hay tập các điệu đàn,… là những việc rất khó khăn, bởi vì tay bạn còn rất yếu, và chưa quen với việc bấm dây nên sẽ rất đau tay! Bởi vì vậy nên cây đàn guitar dành cho người mới chơi cần thực sự chất lượng, đòi hỏi sự kĩ lưỡng trong khâu lựa chọn và không thể bị xem thường! ")]),_c('p',{staticClass:"text-md-left"},[_vm._v(" Tuy nhiên, do bạn là người mới tập chơi đàn, và cũng không phải ai cũng có những “tiền bối” để hỏi kinh nghiệm, nên việc chọn mua đàn guitar thường dựa vào gợi ý của người bán, hoặc đơn giản là… thấy cây đàn guitar này đẹp thì mua… Tuy nhiên, việc mua một cây đàn guitar kiểu này có nhiều khả năng bạn sẽ mua phải những chiếc đàn không tốt, hoặc chí ít là cũng không hợp với dòng nhạc mà mình yêu thích. ")]),_c('p',{staticClass:"text-md-left"},[_vm._v(" Dưới đây, Tempo Music sẽ hướng dẫn các bạn 5 bước chọn mua một chiếc đàn guitar tốt và phù hợp nhất với nhu cầu tập chơi đàn của bạn. ")]),_c('v-img',{attrs:{"src":require("@/assets/articles/bakery/carousel(4).jpg"),"max-height":"400"}})],1),_c('h3',{staticClass:"text-subtitle-2 text-md-left mt-5"},[_vm._v(" I. Xác định thể loại nhạc mà bạn muốn chơi ")]),_c('p',{staticClass:"text-md-left"},[_vm._v(" Trước khi mua đàn thì bạn cần phải xác định được bạn thích chơi thể loại nhạc như thế nào. Lý do rất đơn giản: mỗi thể loại nhạc sẽ có loại đàn dành riêng cho nó, nếu bạn lựa chọn dòng đàn hoàn toàn không hợp với thể loại nhạc mình thích chơi, thì đảm bảo chỉ sau 1 vài tuần hoặc 1 2 tháng là bạn sẽ chán nản và muốn bỏ cuộc ngay! ")]),_c('p',{staticClass:"text-md-left"},[_vm._v(" Vậy thì làm sao để xác định được thể loại nhạc mình muốn chơi và dòng đàn nào là phù hợp? Có 2 trường phái guitar cho bạn lựa chọn: ")]),_c('h3',{staticClass:"text-subtitle-2 text-center mt-5"},[_vm._v("A. Guitar Đệm Hát")]),_c('p',{staticClass:"text-md-left"},[_vm._v(" Đặc điểm của guitar đệm hát là không cần phải chạy nhiều nốt, mà chỉ bấm những tổ hợp nốt cố định và lặp đi lặp lại. Bạn có thể tự đàn tự hát hoặc đàn cho người khác hát. Chơi guitar đệm hát dễ bắt đầu và thành quả nhanh đến hơn (thường thì khoảng 1-2 tháng là bạn có thể tự đàn tự hát được rồi). Hơn nữa khi chơi guitar đệm hát, chắc chắn bạn sẽ có cơ hội giao lưu với bạn bè, đàn cho người khác hát, nên bạn sẽ không cảm thấy cô đơn, mà ngược lại, bạn sẽ có động lực cực kỳ lớn để tiếp tục chơi đàn guitar. ")]),_c('p',{staticClass:"text-md-left"},[_vm._v(" Dấu hiệu để nhận biết bạn phù hợp với guitar đệm hát: ")]),_c('ul',{staticClass:"text-md-left mt-4"},[_c('li',[_vm._v(" Bạn yêu âm nhạc và thích ca hát. Bạn chơi đàn để tự đàn tự hát những bài mình yêu thích. ")]),_c('li',[_vm._v("Bạn thích đàn những bài hát mình thích cho người khác hát.")]),_c('li',[_vm._v("Bạn là người cởi mở và thích giao lưu với mọi người")])]),_c('iframe',{attrs:{"width":"640","height":"360","src":"https://www.youtube.com/embed/VqfhwciL_xE","frameborder":"0","allow":"accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture","allowfullscreen":"allowfullscreen"}}),_c('h3',{staticClass:"text-subtitle-2 text-center mt-5"},[_vm._v("B. Guitar Solo")]),_c('p',{staticClass:"text-md-left"},[_vm._v(" Đặc điểm của guitar solo đó là bạn không cần phải hát, mà chính đôi tay của bạn sẽ tạo nên giai điệu bài hát. Chính vì thế mà guitar solo đòi hỏi bạn phải có nền tảng kỹ thuật tốt, để có thể chạy nốt liên tục toàn bài nhạc. Thành quả của guitar solo rất lâu mới có thể thấy được vì thời gian đầu, người mới tập chơi phải dành tất cả thời gian để rèn luyện các kỹ thuật cơ bản như chạy nốt, học nhạc lý, các nốt nhạc trên cần đàn,…. Hơn thế nữa, thời gian đầu khi mới tập chơi guitar solo, bạn sẽ ít có cơ hội giao lưu với mọi người mà chỉ “một thân một mình” luyện tập thôi! Tuy nhiên một khi đã chơi được guitar solo rồi thì cảm giác sẽ rất là sung sướng, thành quả cũng rất ngọt ngào và tuyệt vời khó tả luôn đấy các bạn ơi! 😀 ")]),_c('p',{staticClass:"text-md-left"},[_vm._v(" Dấu hiệu để nhận biết bạn phù hợp với guitar solo: ")]),_c('ul',{staticClass:"text-md-left mt-4"},[_c('li',[_vm._v(" Bạn yêu âm nhạc và thích chơi lại giai điệu của toàn bộ bài nhạc. ")]),_c('li',[_vm._v(" Mặc dù yêu âm nhạc nhưng bạn lại khá tự ti về giọng hát của mình, hoặc bạn không thích hát. ")]),_c('li',[_vm._v(" Bạn là người khá trầm tính và sống nội tâm, thích một mình một cõi và thả hồn vào âm nhạc. ")])]),_c('iframe',{attrs:{"width":"640","height":"360","src":"https://www.youtube.com/embed/94-KCPOxd2Y","title":"YouTube video player","frameborder":"0","allow":"accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture","allowfullscreen":""}}),_c('p',{staticClass:"text-md-left"},[_vm._v(" Sau khi tìm hiểu sự khác biệt giữa Guitar Đệm Hát và Guitar Solo thì bạn cần lựa chọn sẽ chơi đàn guitar theo thể loại nào: ")]),_c('h3',{staticClass:"text-subtitle-2 text-center mt-5"},[_vm._v(" A. Tôi muốn chơi guitar đệm hát ")]),_c('p',{staticClass:"text-md-left"},[_vm._v(" Nếu bạn thích chơi đệm hát thì Guitar Acoustic là dòng đàn thích hợp nhất dành cho bạn! Với những lý do sau: ")]),_c('ul',{staticClass:"text-md-left mt-4"},[_c('li',[_vm._v(" Âm thanh sắc bén, đanh thép, rõ ràng, phù hợp cho việc các bạn học rải lẫn quạt chả ( Đa số tất cả các bài hát nhạc trẻ hiện nay khi đệm hát đều phải quạt chả thì chơi mới hay được) ")]),_c('li',[_vm._v(" Đa số clip cover hoặc hướng dẫn tự học guitar đệm hát trên mạng đều dùng đàn guitar acoustic. Nếu bạn chọn dòng đàn khác và sau đó học theo giáo trình dùng acoustic thì âm thanh không tương đồng với nhau. Bạn cảm thấy đàn mình tiếng bì tù và không rõ như đàn của bạn bè, dần dần bạn sẽ bỏ cuộc vì tưởng rằng mình không có năng khiếu. ")]),_c('li',[_vm._v(" Đàn guitar acoustic có âm thanh vang và rõ, cực kỳ thích hợp khi chơi trước đám đông, các buổi offline, ca nhạc du ca,… ")]),_c('li',[_vm._v(" Đàn guitar acoustic có cần đàn nhỏ, giúp bấm hợp âm dễ dàng và đơn giản hơn. Trong khi đàn Classic thì cần đàn to hơn nhiều, khiến bạn vừa đau vừa mỏi (đặc biệt với nữ). ")])]),_c('h3',{staticClass:"text-subtitle-2 text-center mt-5"},[_vm._v(" B. Tôi muốn chơi guitar solo ")]),_c('p',{staticClass:"text-md-left"},[_vm._v(" Nếu bạn thích chơi guitar solo thì Guitar Classic là dòng đàn thích hợp nhất dành cho bạn! Với những lý do sau: ")]),_c('ul',{staticClass:"text-md-left mt-4"},[_c('li',[_vm._v(" Âm thanh trầm, ấm, sâu lắng, thích hợp để chơi các dòng nhạc nhẹ nhàng, trữ tình. ")]),_c('li',[_vm._v(" Dây bấm nhẹ, dễ dàng để bấm các nốt nhạc trên cần đàn và đi nốt suốt nguyên bài nhạc ")]),_c('li',[_vm._v(" Bản cần đàn to, khoảng cách ngăn tương đối rộng, phù hợp với đặc thù của chơi solo cổ điển, vì khi đánh nốt sẽ dễ dàng hơn, không bị đè dây khác. ")]),_c('li',[_vm._v(" Đàn guitar classic thích hợp để chơi một mình, trong những không gian tĩnh lặng, để người chơi có thể thả hồn vào âm nhạc trữ tình ")])]),_c('h3',{staticClass:"text-subtitle-2 text-md-left mt-5"},[_vm._v(" II. Xác định chất liệu gỗ làm đàn ")]),_c('p',{staticClass:"text-subtitle-2 text-md-left"},[_vm._v(" Hiện này đàn guitar trên thị trường Việt Nam được chia thành 3 loại cơ bản theo loại gỗ như sau: ")]),_c('ol',[_c('li',[_vm._v("Đàn guitar từ "),_c('strong',[_vm._v("gỗ nguyến miếng")])]),_c('li',[_vm._v("Đàn guitar làm từ "),_c('strong',[_vm._v("gỗ ép")])]),_c('li',[_vm._v(" Đàn guitar làm từ "),_c('strong',[_vm._v("ván ép")]),_vm._v(" (gỗ tạp được chế xuất từ mụn cưa ép lại) ")])]),_c('h4',{staticClass:"text-subtitle-2 text-md-left mt-5"},[_vm._v(" 1. Gỗ nguyên miếng (Solid Wood) ")]),_c('p',{staticClass:"text-subtitle-2 text-md-left"},[_vm._v(" Là gỗ tự nhiên, được xẻ trực tiếp từ cây gỗ tự nhiên. Qua các khâu chế biến thì gỗ nguyên miếng sẽ được dùng làm mặt trước, mặt lưng và hông đàn guitar. ")]),_c('ul',[_c('li',[_vm._v(" Ưu điểm: Âm thanh được đa số các nhạc công đánh giá là hay hơn, đầy đủ các thang âm hơn gỗ ép. Nếu bảo quản tốt đàn càng chơi càng hay (đây được gọi là hiện tượng vỡ tiếng, đa số chỉ có các dòng đàn Solid Wood mới có) ")]),_c('li',[_vm._v(" Nhược điểm: Vì là gỗ nguyên miếng (được làm từ duy nhất 1 miếng gỗ) nên đàn Solid Wood rất nhạy cảm với môi trường, dễ bị ảnh hưởng bởi thay đổi thời tiết, độ bền kém hơn gỗ ép, đòi hỏi người chơi phải có kiến thức bảo quản đàn tốt nếu muốn chơi được lâu. Vì là gỗ nguyên miếng nên gia công khó và đương nhiên là mắc tiền hơn các loại gỗ khác. ")])]),_c('h4',{staticClass:"text-subtitle-2 text-md-left mt-5"},[_vm._v("2.Gỗ ép (Laminate Wood)")]),_c('p',{staticClass:"text-subtitle-2 text-md-left"},[_vm._v(" Là loại gỗ 3 lớp: 2 lớp bên ngoài là những lớp gỗ thịt rất mỏng, dán vào lõi là loại gỗ khác rẻ tiền hơn (gỗ tạp). Chất lượng gỗ laminate phụ thuộc vào việc chế tạo và xử lí . ")]),_c('ul',[_c('li',[_vm._v(" Ưu điểm: cũng mang những tính chất âm như loại gỗ thịt tương ứng, nhưng không hay bằng vì là loại đàn được ghép từ nhiều lớp gỗ lại với nhau. Guitar Laminate Wood ít bị ảnh hưởng bởi thời tiết hơn, độ bền cao hơn, gia công dễ dàng nên giá thành thường rẻ hơn guitar Solid Wood. Các thương hiệu nổi tiếng như guitar Fender, Taylor, Takamine cũng sử dụng những loại gỗ ép này cho những dòng đàn guitar giá rẻ của họ. ")]),_c('li',[_vm._v(" Nhược điểm: gần như không có khả năng vỡ tiếng, âm thanh không hay bằng gỗ thịt trong cùng tầm giá. ")])]),_c('h4',{staticClass:"text-subtitle-2 text-md-left mt-5"},[_vm._v("3. Ván ép")]),_c('p',{staticClass:"text-s