… ")])]),_c('v-divider',{staticClass:"mt-5",attrs:{"inset":""}}),_c('h3',{staticClass:"text-subtitle-2 text-center mt-5"},[_vm._v(" Bạn Vẫn Còn Bối Rối Và Chưa Thể Hình Dung Được Mình Nên Mua Dòng Đàn Nào Cho Người Mới Tập? ")]),_c('h3',{staticClass:"text-subtitle-2 text-center mt-5"},[_vm._v(" Gọi ngay : 097 919 03 03 ")]),_c('h3',{staticClass:"text-subtitle-2 text-center mt-5"},[_vm._v(" Để được tư vấn hoàn toàn MIỄN PHÍ ")])],1)],1)}
